.bodyWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  float: left;
  width: 100%;
}

.mediaOnly{
  display: none;
}

.sideBar {
  padding: 8px;
  width: 36%;
  background-color: #fbfcff;
  color: #274156;
  text-align: justify;
}

.sideBar p {
  font-size: 1em;
}

.content-wrap h3, .content-wrap h4 {
  text-align: center;
}

.mainContent {
  width: 65%;
  display:-webkit-flex;
  display:flex;
  background-color: #AFD0D6;
}

.writingBox {
  width: 100%;
  height: 200px;
  border: 2px solid #000;
  border-radius: 10px;
  background-color: #fbfcff;
  resize: none;
}

.writingSpace {
  background-color: #1C6E8C;
  color: #AFD0D6;
  padding: 50px;
}

.twitterBtn {
  background-color: #00aced;
  color: #fff;
  padding: 7px;
  border-radius: 30px;
  box-shadow: 2px 2px #AFD0D6;
  border: 1px solid #fff;
}
.twitterBtn:hover {
  background-color: #0084b4;
}


@media only screen and (max-width: 768px) {
  .mediaOnly{
    display:inherit;
  }
  .bodyWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
  }

  .mainContent {
    width: 100%;
  }

  .writingSpace {
    width: 100%;
  }

  .sideBar {
    padding: 8px;
    width: 100%;
  }

}



